import styled from 'styled-components';
import { useState } from 'react';
import { ToggleSVG } from '@/components/common/svg/Toggle';

function SubscribeToggle() {
  const [ toggle, setToggle ] = useState(false)

  const toggleSubscribe = () => {
    setToggle(!toggle);
  }

  return (
    <SubscribeToggleStyled>
      <div className="toggle" onClick={toggleSubscribe}>
        {toggle ? (
            <div className="subs-on">
              <ToggleSVG /> 구독 중
            </div>
          )
          :
          (
            <div className="subs-off">
              <ToggleSVG /> 구독 일시정지 중
            </div>
          )
        }
      </div>
    </SubscribeToggleStyled>
  );
}

const SubscribeToggleStyled = styled.div`
    .subs-off {
        svg {
            rect {
                fill: ${({ theme }) => theme.color.border};;
                stroke: white;
            }
        }
    }

`;

export default SubscribeToggle;